.overview{padding:80px 0;background:linear-gradient(to bottom, var(--quaternary-light) 30%, var(--blue-dark) 100%)}.content .row{gap:24px 0}.card{background:var(--white);border-radius:12px;padding:.75rem;height:100%;display:flex;flex-direction:column;transition:all .3s ease;border:1px solid rgba(26,80,119,.1)}.card:hover{transform:translateY(-4px);box-shadow:0 8px 30px rgba(0,0,0,.15);border-color:var(--primary-light)}.cardHeader{margin-bottom:1.25rem}.cardIcon{width:100%;aspect-ratio:600/400;margin-bottom:0;overflow:hidden;border-radius:8px}.cardIcon img{width:100%;height:100%;display:block;object-fit:cover}.cardBody{flex:1;margin-bottom:1rem}.cardTitle{font-size:20px;color:var(--text-indigo);margin-bottom:16px;line-height:1.3;min-height:52px;display:flex}.cardDescription{color:var(--gray-800);line-height:1.6;margin-bottom:0}.cardFooter{display:flex;justify-content:flex-end;align-items:center;gap:8px}.readMoreBtn{display:inline-flex;align-items:center;gap:8px;padding:12px 24px;font-weight:600;transition:all .3s ease;border-radius:10px;padding:10px 20px;font-size:14px;font-weight:600;text-decoration:none;transition:all .3s ease;border:none;font-size:1rem}.readMoreBtn::after{content:"→";font-size:1.5rem;line-height:1;transition:transform .3s ease}.readMoreBtn:hover::after{transform:translateX(4px)}@media(max-width: 991px){.overview{padding:60px 0}.card{margin-bottom:24px}.cardTitle{font-size:18px;min-height:auto}}@media(max-width: 767px){.overview{padding:40px 0}.card{padding:24px 20px}.cardTitle{font-size:16px}.cardDescription{font-size:14px}}