.header{position:sticky;top:0;left:0;right:0;z-index:1000;transition:all .3s ease;padding:16px 0}.header.visible{transform:translateY(0)}.header.hidden{transform:translateY(-100%)}.wrapper{background-color:var(--white);margin:0 auto;display:flex;flex-direction:row;align-items:center;justify-content:space-between;height:70px;border-radius:1rem;box-shadow:0 0 10px 0 rgba(0,0,0,.25);padding:.5rem 1rem;width:100%}@media(max-width: 768px){.wrapper{padding:0 15px;height:60px}}.logo{width:120px;height:50px;display:flex;align-items:center}@media(max-width: 768px){.logo{width:100px;height:42px}}@media(max-width: 480px){.logo{width:90px;height:38px}}.logoImage{width:60px;height:60px;object-fit:contain}.logoButton{background:none;border:none;cursor:pointer;padding:0;display:flex;flex-direction:column;align-items:flex-start;transition:transform .2s ease}.logoButton:hover{transform:scale(1.05)}.logoButton:focus{outline:2px solid #2f8c8a;outline-offset:2px;border-radius:4px}.logoText{color:#2f8c8a;font-size:24px;font-weight:700;line-height:1;letter-spacing:-0.5px}@media(max-width: 768px){.logoText{font-size:20px}}.logoSubtext{color:var(--text-lilac);font-size:12px;font-weight:500;letter-spacing:2px;margin-top:-2px}@media(max-width: 768px){.logoSubtext{font-size:10px}}.nav{flex:1;display:flex;justify-content:center;margin:0 40px}@media(max-width: 768px){.nav{display:none}}.navList{display:flex;list-style:none;margin:0;padding:0;gap:30px}@media(max-width: 1024px){.navList{gap:20px}}.navItem{background:none;border:none;cursor:pointer;color:var(--text-blue);font-size:16px;font-weight:500;padding:8px 12px;border-radius:6px;transition:all .2s ease;position:relative}.navItem:hover{color:#2f8c8a;background:rgba(47,140,138,.1)}.navItem:focus{outline:2px solid #2f8c8a;outline-offset:2px}.navItem:active{transform:translateY(1px)}@media(max-width: 1024px){.navItem{font-size:14px;padding:6px 10px}}.languageSelector{flex-shrink:0}.languageSelect{padding:8px 12px;border:2px solid #2f8c8a;border-radius:6px;background:#fff;color:#2f8c8a;font-size:14px;font-weight:600;cursor:pointer;transition:all .2s ease;min-width:60px}.languageSelect:hover{background:#2f8c8a;color:#fff;transform:translateY(-1px);box-shadow:0 4px 8px rgba(47,140,138,.3)}.languageSelect:focus{outline:none;box-shadow:0 0 0 3px rgba(47,140,138,.3)}@media(max-width: 768px){.languageSelect{padding:6px 8px;font-size:12px;min-width:50px}}@media(max-width: 768px){.container{justify-content:space-between}.mobileMenuToggle{display:block;background:none;border:none;cursor:pointer;padding:5px;color:#2f8c8a}.mobileMenuToggle:focus{outline:2px solid #2f8c8a;outline-offset:2px}}