.footer{background:linear-gradient(135deg, var(--primary-dark) 0%, var(--primary) 100%);color:var(--white);padding:3rem 0 1rem}.footer .footerContent{display:grid;grid-template-columns:2fr 1fr 1fr 1fr;gap:3rem;margin-bottom:2rem}.footer .contactInfo .title{font-size:1.8rem;margin-bottom:1.5rem;color:var(--secondary-color)}.footer .contactInfo .contactDetails .contactItem{margin-bottom:1.5rem}.footer .contactInfo .contactDetails .contactItem strong{display:block;margin-bottom:.5rem;color:var(--secondary-color)}.footer .contactInfo .contactDetails .contactItem p{margin:0;line-height:1.6}.footer .contactInfo .contactDetails .contactItem p a{color:var(--white)}.footer .contactInfo .contactDetails .contactItem p a:hover{color:var(--secondary-color)}.footer .quickLinks h3,.footer .quickLinks h4,.footer .services h3,.footer .services h4,.footer .social h3,.footer .social h4{font-size:1.4rem;margin-bottom:1rem;color:var(--secondary-color)}.footer .quickLinks h4,.footer .services h4,.footer .social h4{font-size:1.1rem;margin-top:1.5rem}.footer .quickLinks ul,.footer .services ul,.footer .social ul{list-style:none}.footer .quickLinks ul li,.footer .services ul li,.footer .social ul li{margin-bottom:.8rem}.footer .quickLinks ul li a,.footer .services ul li a,.footer .social ul li a{color:hsla(0,0%,100%,.9);transition:color .3s ease}.footer .quickLinks ul li a:hover,.footer .services ul li a:hover,.footer .social ul li a:hover{color:var(--secondary-color)}.footer .socialLinks{margin-bottom:1.5rem}.footer .socialLinks .socialLink{display:inline-block;padding:.8rem 1.5rem;background:hsla(0,0%,100%,.1);border-radius:8px;color:var(--white);transition:all .3s ease}.footer .socialLinks .socialLink:hover{background:hsla(0,0%,100%,.2);color:var(--secondary-color)}.footer .languageLinks{display:flex;flex-direction:column;gap:.5rem}.footer .languageLinks a{color:hsla(0,0%,100%,.8);font-size:.9rem;transition:color .3s ease}.footer .languageLinks a:hover,.footer .languageLinks a.active{color:var(--secondary-color);font-weight:600}.footer .footerBottom{border-top:1px solid hsla(0,0%,100%,.2);padding-top:2rem;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;gap:1rem}.footer .footerBottom .copyright p,.footer .footerBottom .credentials p{margin:0;font-size:.9rem;color:hsla(0,0%,100%,.8)}.footer .footerBottom .credentials{font-style:italic}@media(max-width: 1024px){.footer .footerContent{grid-template-columns:1fr 1fr;gap:2rem}}@media(max-width: 768px){.footer{padding:2rem 0 1rem}.footer .footerContent{grid-template-columns:1fr;gap:2rem;text-align:center}.footer .contactInfo .title{font-size:1.5rem}.footer .footerBottom{flex-direction:column;text-align:center;gap:1rem}.footer .languageLinks{flex-direction:row;justify-content:center;gap:1rem}}@media(max-width: 480px){.footer .socialLinks .socialLink{padding:.6rem 1.2rem;font-size:.9rem}}