.partnerships .header{text-align:center;margin-bottom:4rem}.partnerships .title{color:var(--primary);font-size:2.5rem;margin-bottom:1rem}.partnerships .subtitle{font-size:1.2rem;color:var(--text-primary);max-width:700px;margin:0 auto}.partnerships .partnershipsGrid{margin-bottom:4rem}.partnerships .partnerCard{background:var(--gray-light);padding:2rem;border-radius:12px;text-align:center;transition:transform .3s ease,box-shadow .3s ease}.partnerships .partnerCard:hover{transform:translateY(-5px);box-shadow:0 10px 25px rgba(0,0,0,.1)}.partnerships .logoPlaceholder{width:80px;height:80px;background:var(--primary);border-radius:50%;display:flex;align-items:center;justify-content:center;margin:0 auto 1.5rem;color:#fff;font-size:1.5rem;font-weight:bold}.partnerships .partnerName{color:var(--text-primary);font-size:1.2rem;margin-bottom:.5rem}.partnerships .partnerCategory{color:var(--gray-dark);font-size:.9rem;font-style:italic}.partnerships .certifications{background:var(--secondary-color);padding:3rem;border-radius:16px;text-align:center}.partnerships .certifications h3{color:var(--primary);font-size:2rem;margin-bottom:2rem}.partnerships .certifications .certList{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:1.5rem}.partnerships .certifications .certItem{background:var(--white);padding:1.5rem;border-radius:8px;box-shadow:0 5px 15px rgba(0,0,0,.1)}.partnerships .certifications .certItem strong{color:var(--primary);display:block;font-size:1.1rem;margin-bottom:.5rem}@media(max-width: 768px){.partnerships .title{font-size:2rem}.partnerships .certifications{padding:2rem}.partnerships .certifications h3{font-size:1.5rem}.partnerships .certifications .certList{grid-template-columns:1fr;gap:1rem}}